import { Component } from 'react'
import Link from 'next/link'
import firebase from '../components/firebase'

class Index extends Component {

  constructor(props) {
    super(props)
    this.state = {
       product: '',
       productid: '',
       price: '',
       state: '',
       warranty: '',
       number: '',
     }
  }

  componentWillMount(){
  /* Create reference to messages in Firebase Database */
  let productsRef = firebase.database().ref('products').orderByKey().limitToLast(100);
  productsRef.on('child_added', snapshot => {
    /* Update React state when message is added at Firebase Database */
    let product = { text: snapshot.val(), id: snapshot.key };
    this.setState({ products: [product].concat(this.state.product) });
  })
}
addProduct(e){
  e.preventDefault(); // <- prevent form submit from reloading the page
  /* Send the message to Firebase */
  firebase.database().ref('product').push( this.inputEl.value );
  this.inputEl.value = ''; // <- clear the input
}

  render () {
    return (
      <div>
        <h1>Sonderpostenliste</h1>
        <Link href="/addproduct">
          <a>Artikel hinzuf체gen</a>
        </Link>

        <form>
        <input type="text" name="name" placeholder="Produktname" onChange={this.handleChange} value={this.state.name} />
        <input type="text" name="price" placeholder="Preis" onChange={this.handleChange} value={this.state.price} />
        <input type="text" name="productid" placeholder="ID" onChange={this.handleChange} value={this.state.productid} />
        <select name="state" onChange={this.handleChange} value={this.state.state}>
          <option value="new">Neu</option>
          <option value="used">R체ckl채ufer</option>
          <option value="demo">Demoger채t</option>
        </select>
       <input type="submit"/>
       <ul>

       </ul>
     </form>
      </div>
    )
  }

}

export default Index



